* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* 页面结构 */

.container,
.flex-column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.container {
  position: relative;
  left: 0;
}

.mask {
  height: 100%;
  width: 0;
  position: absolute;
  background-color: rgba(0, 0, 0, .7);
  opacity: 0;
  -webkit-transition: opacity .2s ease;
  -o-transition: opacity .2s ease;
  transition: opacity .2s ease;
}

.about-body {
  position: fixed;
  height: 100%;
}

.about-body.show {
  right: 0;
}


.container.offset {
  left: -100%;
}

.about-body.hide {
  right: -100%;
}

.about-body {
  width: 100%;
}

@media screen and (min-width: 400px) {
  .container.offset {
    left: -400px;
  }
  .about-body.hide {
    right: -400px;
  }
  .about-body {
    width: 400px;
  }
}

/* 通用CSS */

.flex-normal {
  display: -ms-flexbox;
  display: flex;
}

.back-white {
  background-color: white;
}

.font-white {
  color: white;
}

.font-gray {
  color: gray;
}

.background-color {
  background-color: #f3f3f3;
}

.flex-hv-center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.flex-c-center {
  -ms-flex-pack: center;
      justify-content: center;
}

.flex-v-center {
  -ms-flex-align: center;
      align-items: center;
}

.self-center {
  -ms-flex-item-align: center;
      align-self: center;
}

.text-center {
  text-align: center;
}

.flex-wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.mr-auto {
  margin-right: auto;
}

.m-auto {
  margin: auto;
}

.shadow {
  -webkit-box-shadow: 0 0 55px black;
          box-shadow: 0 0 55px black;
}

.light-shadow {
  -webkit-box-shadow: 0 0 1px #ddd;
          box-shadow: 0 0 1px #ddd;
}

.transition {
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.transition-slowly {
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
}



